<template>
  <view class="demo">
    <h2></h2>
    <nut-cell
      title=""
      :desc="one"
      is-link
      @click="showAddress"
    ></nut-cell>

    <nut-address
      v-model:visible="normal"
      :province="province"
      :city="city"
      :country="country"
      :town="town"
      @change="cal => onChange(cal, 'normal')"
      @close="close1"
      custom-address-title=""
    ></nut-address>

    <h2></h2>
    <nut-cell
      title=""
      :desc="two"
      is-link
      @click="showAddressExist"
    ></nut-cell>

    <nut-address
      v-model:visible="exist"
      type="exist"
      :exist-address="existAddress"
      @change="cal => onChange(cal, 'exist')"
      @close="close2"
      :is-show-custom-address="false"
      @selected="selected"
      exist-address-title=""
    ></nut-address>

    <h2></h2>
    <nut-cell
      title=""
      :desc="three"
      is-link
      @click="showCustomImg"
    ></nut-cell>

    <nut-address
      v-model:visible="customImg"
      type="exist"
      :exist-address="existAddress"
      @change="cal => onChange(cal, 'customImg')"
      @close="close3"
      :is-show-custom-address="false"
      @selected="selected"
      :default-icon="defaultIcon"
      :selected-icon="selectedIcon"
      :close-btn-icon="closeBtnIcon"
    ></nut-address>

    <h2></h2>
    <nut-cell
      title=""
      :desc="four"
      is-link
      @click="showAddressOther"
    ></nut-cell>

    <nut-address
      v-model:visible="other"
      type="exist"
      :exist-address="existAddress"
      :province="province"
      :city="city"
      :country="country"
      :town="town"
      :back-btn-icon="backBtnIcon"
      @change="cal => onChange(cal, 'other')"
      @close="close4"
      @selected="selected"
      custom-and-exist-title=""
      @switch-module="switchModule"
      @close-mask="closeMask"
    ></nut-address>
  </view>
</template>

<script lang="ts">

import { reactive, ref, toRefs } from 'vue';

import { createComponent } from './../../../../../../packages/utils/create';
import Address from './index.taro.vue';
import Cell from './../cell/index.taro.vue';
const { createDemo } = createComponent('address');

interface CalBack {
  next: string;
  value: string;
  custom: string;
}
interface RegionData {
  name: string;
  [key: string]: any;
}
interface CalResult {
  type: string;
  data: AddressResult;
}
interface AddressList {
  id?: string | number;
  provinceName: string;
  cityName: string;
  countyName: string;
  townName: string;
  addressDetail: string;
  selectedAddress: boolean;
}
interface AddressResult extends AddressList {
  addressIdStr: string;
  addressStr: string;
  province: RegionData[];
  city: RegionData[];
  country: RegionData[];
  town: RegionData[];
}
export default createDemo({
   components: {
    'nut-address': Address,
    'nut-cell': Cell
  },
  props: {},
  setup() {
    const address = reactive({
      province: [
        { id: 1, name: '' },
        { id: 2, name: '' },
        { id: 3, name: '' },
        { id: 4, name: '' }
      ],
      city: [
        { id: 7, name: '' },
        { id: 8, name: '' },
        { id: 9, name: '' },
        { id: 6, name: '' }
      ],
      country: [
        { id: 3, name: '' },
        { id: 9, name: '' },
        { id: 4, name: '' }
      ],
      town: []
    });

    const showPopup = reactive({
      normal: false,
      exist: false,
      customImg: false,
      other: false
    });

    const icon = reactive({
      selectedIcon: 'heart-fill',
      defaultIcon: 'heart1',
      closeBtnIcon: 'close',
      backBtnIcon: 'left'
    });

    const existAddress = ref([
      {
        id: 1,
        addressDetail: '',
        cityName: '',
        countyName: '',
        provinceName: '',
        selectedAddress: true,
        townName: ''
      },
      {
        id: 2,
        addressDetail: '',
        cityName: '',
        countyName: '',
        provinceName: '',
        selectedAddress: false,
        townName: ''
      },
      {
        id: 3,
        addressDetail: '',
        cityName: '',
        countyName: '18',
        provinceName: '',
        selectedAddress: false,
        townName: ''
      }
    ]);

    const text = reactive({
      one: '',
      two: '',
      three: '',
      four: ''
    });

    const showAddress = () => {
      showPopup.normal = !showPopup.normal;
    };

    const onChange = (cal: CalBack, tag: string) => {
      const name = (address as any)[cal.next];
      if (name.length < 1) {
        (showPopup as any)[tag] = false;
      }
    };
    const close1 = (val: CalResult) => {
      console.log(val);
      text.one = val.data.addressStr;
    };

    const showAddressExist = () => {
      showPopup.exist = true;
    };

    const close2 = (val: CalResult) => {
      console.log(val);
      if (val.type == 'exist') {
        const {
          provinceName,
          cityName,
          countyName,
          townName,
          addressDetail
        } = val.data;
        text.two =
          provinceName + cityName + countyName + townName + addressDetail;
      } else {
        text.two = val.data.addressStr;
      }
    };
    const selected = (
      prevExistAdd: AddressList,
      nowExistAdd: RegionData,
      arr: AddressList[]
    ) => {
      console.log(prevExistAdd);
      console.log(nowExistAdd);
    };

    const showAddressOther = () => {
      showPopup.other = true;
    };
    const showCustomImg = () => {
      showPopup.customImg = true;
    };

    const close3 = (val: CalResult) => {
      console.log(val);
      if (val.type == 'exist') {
        const {
          provinceName,
          cityName,
          countyName,
          townName,
          addressDetail
        } = val.data;
        text.three =
          provinceName + cityName + countyName + townName + addressDetail;
      } else {
        text.three = val.data.addressStr;
      }
    };

    const close4 = (val: CalResult) => {
      console.log(val);
      if (val.type == 'exist') {
        const {
          provinceName,
          cityName,
          countyName,
          townName,
          addressDetail
        } = val.data;
        text.four =
          provinceName + cityName + countyName + townName + addressDetail;
      } else {
        text.four = val.data.addressStr;
      }
    };

    const switchModule = (val: CalResult) => {
      if (val.type == 'custom') {
        console.log('');
      } else {
        console.log('');
      }
    };

    const closeMask = (val: CalResult) => {
      console.log('', val);
    };

    return {
      showAddress,
      showPopup,
      onChange,
      close1,
      showAddressExist,
      close2,
      selected,
      existAddress,
      showAddressOther,
      showCustomImg,
      close3,
      close4,
      switchModule,
      closeMask,
      ...toRefs(icon),
      ...toRefs(text),
      ...toRefs(showPopup),
      ...toRefs(address)
    };
  }
});
</script>

<style lang="scss" scoped>
.demo {
  .nut-cell {
    align-items: center;

    .nut-cell__value {
      margin-right: 8px;
    }
  }
}
</style>
